<template>
  <div class="container">
    <h5 class="display-4">This content is still working-in-progress</h5>
    <div class="row">
      <div id="side-bar" class="sidebar col-sm-auto invisible">
        <nav id="featuresnav" class="navbar navbar-expand-sm navbar-toggleable-sm navbar-light bg-white border-bottom box-shadow mb-3">
          <div class="flex-column">
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsenav" aria-controls="navbarSupportedContent"
                    aria-expanded="false" aria-label="Toggle navigation">
              <span class="navbar-toggler-icon"></span>
            </button>
            <nav id="collapsenav" class="navbar navbar-collapse collapse d-sm-inline-flex">
              <div class="nav flex-column nav-pills" aria-orientation="vertical" v-scroll-spy-active="{class: 'active'}" v-scroll-spy-link data-scroll-spy-id="mainscroll">
                <a class="nav-link">Decompilation</a>
                <nav class="navbar d-sm-inline-flex">
                  <div class="nav flex-column nav-pills" aria-orientation="vertical" v-scroll-spy-active="{class: 'active'}" v-scroll-spy-link data-scroll-spy-id="decomptiationscroll">
                    <a class="nav-link">Normal Java Archive</a>
                  </div>
                </nav>
              </div>
            </nav>
          </div>
        </nav>
      </div>
      <div v-if="isbig" id="placehold" class="sizeprovider col col-sm-4 col-md-3 col-lg-3 col-xl-3"></div>
      <div class="col col-md-9 col-lg-9 col-xl-9">
        <div data-spy="scroll" v-scroll-spy data-scroll-spy-id="mainscroll">
          <div>
            <h3>Decompilation</h3>
            <p>FernflowerUI can help you decompile Jar files without using the disappointing CLI.</p>
            <p>There are three kinds of decompilation,which are named:Normal Java Archive,Normal Java Classes and Classes inside a Archive</p>
            <div v-scroll-spy data-scroll-spy-id="decomptiationscroll">
              <div>
                <h5>Normal Java Archive</h5>
              </div>
              <p>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
var viewmodel = null
export default {
  name: 'Features',
  data: function () {
    return {
      isbig: true
    }
  },
  beforeRouteEnter (to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    next(vm => {
      var linkto = document.getElementById(vm.$route.name.toLowerCase() + '-tab')
      linkto.className = 'nav-link active'
      viewmodel = vm
      var sidebar = document.getElementById('side-bar')
      var sizeprovider = document.getElementById('placehold')
      viewmodel.isbig = true
      if (window.innerWidth < 576) {
        viewmodel.isbig = false
        sidebar.style.width = sidebar.style.height = null
      }
      if (sidebar != null && sizeprovider != null) {
        sidebar.className = 'sidebar col-sm-auto'
        sidebar.style.height = sizeprovider.offsetHeight.toString() + 'px'
        sidebar.style.width = sizeprovider.offsetWidth.toString() + 'px'
      }
    })
    $(window).resize(function () {
      var sidebar = document.getElementById('side-bar')
      var sizeprovider = document.getElementById('placehold')
      viewmodel.isbig = true
      if (window.innerWidth < 576) {
        viewmodel.isbig = false
        sidebar.style.width = sidebar.style.height = null
      }
      if (sidebar != null && sizeprovider != null) {
        sidebar.className = 'sidebar col-sm-auto'
        sidebar.style.height = sizeprovider.offsetHeight.toString() + 'px'
        sidebar.style.width = sizeprovider.offsetWidth.toString() + 'px'
      }
    })
  },
  beforeRouteLeave (to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    var sidebar = document.getElementById('side-bar')
    sidebar.className = 'sidebar col-md-3 col-lg-3 col-xl-3 col-sm-auto invisible'
    next()
  }
}
</script>

<style scoped>
  @media (min-width: 576px) {
    .sidebar{
      position:fixed;
      z-index: 1000;
    }
    .sizeprovider{
    visibility: visible;
    }
  }
  @media (max-width: 576px){
    .sizeprovider{
    visibility: hidden;
    }
  }
</style>
