<template>
  <div class="download">
    <div class="container">
      <div>
        <div class="text-center" v-if="treeUnLoaded">
          <div class="spinner-border text-primary" role="status">
          </div>
          <h5>
            Loading...
          </h5>
        </div>
        <div v-else>
          <h3 class="display-4">
            Download FernflowerUI
          </h3>
          <div class="alert alert-info" role="alert">
            Click on the links to download,or manually download via <a href="https://github.com/6168218c/FernflowerUI/releases">GitHub</a>
          </div>
        </div>
      </div>
      <div class="text-left" id="treeviewcontainer">
        <div id="treeview"></div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import 'open-iconic/font/css/open-iconic-bootstrap.css'
import 'bootstrap-treeview/dist/bootstrap-treeview.min'
import 'bootstrap-treeview/dist/bootstrap-treeview.min.css'
import crypto from 'crypto'
export default {
  name: 'Download',
  data: function () {
    return {
      treeUnLoaded: true
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      var linkto = document.getElementById(vm.$route.name.toLowerCase() + '-tab')
      linkto.className = 'nav-link active'
      const decipher = crypto.createDecipheriv('aes-128-cbc', 'kevinsun6168218c', 'kevinsun6168218c')
      var decrypted = decipher.update('hfOF4rVPiqhfsyY0Y9tSXAtRKtExI8KQN6xtZPwvmYMhWn8HGc0UZaAL/YvQAAMM', 'base64', 'utf8')
      decrypted += decipher.final('utf8')
      $.ajax({
        url: 'https://api.github.com/repos/6168218c/FernflowerUI/releases?access_token=' + decrypted,
        dataType: 'json',
        type: 'get',
        success: function (data) {
          document.getElementById('treeviewcontainer').className = 'text-left'
          vm.treeUnLoaded = false
          var treedata = []
          $.each(data, function (id, item) {
            var children = []
            $.each(item.assets, function (id, child) {
              var chi = {
                text: child.name,
                href: child.browser_download_url,
                selectable: false
              }
              children.push(chi)
            })
            var parent = {
              text: item.tag_name,
              nodes: children,
              selectable: false
            }
            treedata.push(parent)
          })
          $('#treeview').treeview({
            color: '#428bca',
            expandIcon: 'oi oi-chevron-right',
            collapseIcon: 'oi oi-chevron-bottom',
            enableLinks: true,
            data: treedata
          })
        },
        error: function (e) {
          alert('Error with' + e)
        }
      })
    })
  },
  beforeRouteLeave (to, from, next) {
    // 导航离开该组件的对应路由时调用
    // 可以访问组件实例 `this`
    this.treeUnLoaded = true
    document.getElementById('treeviewcontainer').className = 'text-left invisible'
    next()
  }
}
</script>

<style scoped>
</style>
