<script>
export default {
  name: 'Home',
  beforeRouteEnter (to, from, next) {
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    next(vm => {
      var name = vm.$route.name
      if (vm.$route.name === '') {
        name = 'Home'
      }
      var linkto = document.getElementById(name.toLowerCase() + '-tab')
      linkto.className = 'nav-link active'
    })
  }
}
</script>

